export default {
  name: 'uni-calendar-item',
  props: {
    /**
     * 当前日期
     */
    canlender: {
      type: null,
      default: () => {
        return {};
      }
    },
    lunar: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {};
  },
  methods: {
    selectDays(week, index, ischeck, isDay, lunar) {
      this.$emit('selectDays', {
        week,
        index,
        ischeck,
        isDay,
        lunar
      });
    }
  }
};
